.l-home {
  position: relative;
  min-height: 100vh;
  padding: 70px 20px 20px;
  padding-left: 300px; }
  .l-home__title {
    position: absolute;
    top: 55px;
    margin: 0;
    color: #E40B01;
    font-family: "Noto Serif", serif;
    font-size: 45px;
    font-weight: bold;
    text-align: right; }
  .l-home__grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 40px; }
  .l-home__grid-box {
    width: calc((100% / 3) - 20px);
    height: 300px;
    margin-bottom: 20px;
    background-position: 50% 50%;
    background-size: cover;
    cursor: pointer; }
  .l-home__grid-detail {
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 20px 40px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #FFF;
    color: #111; }

