@import '../../styles/index';

.l-about-us {
    position: relative;
    min-height: 100vh;
    padding: 70px 20px 20px;
    padding-left: calc(100% / 5);

    &__title {
        position: absolute;
        top: 55px;
        margin: 0;
        color: $secondary-color;
        font-family: $primary-font;
        font-size: 45px;
        font-weight: bold;
        text-align: right;
    }

    &__grid {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-top: 40px;
    }

    &__grid-box {
        width: calc((100% / 4) - 20px);
        height: 400px;
        margin-bottom: 20px;
        background-position: 50% 50%;
        background-size: cover;
        cursor: pointer;
    }

    &__grid-detail {
        position: fixed;
        top: 50%;
        left: 50%;
        padding: 20px 40px;
        transform: translate(-50%, -50%);
        background-color: $white;
        color: $black;
    }
}
