@import '../../styles/index';

.c-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 200px;
    padding: 70px 20px 20px;
    z-index: 99;

    a {
        color: $black;
        text-decoration: none;
    }

    &__title {
        margin: 0;
        font-family: $primary-font;
        font-size: 30px;
        font-weight: bold;
    }

    &__list {
        padding-left: 10px;
        list-style-type: none;

        li {
            margin-top: 30px;

            a {
                font-size: 18px;
            }
        }
    }

    &__list-social {
        padding-left: 10px;
        border-top: 1px solid $secondary-color;
        list-style-type: none;

        li {
            display: block;
            margin-top: 20px;
            padding-left: 10px;
        }
    }
}
